<template>
  <div class="login-wrap">
    <div class="login_setting">
      <van-icon name="setting-o" @click="showTypeChange(0)"/>
    </div>
    <div class="logo"></div>
    <div class="loginForm">
      <div class="formItem">
        <van-field v-model="accountNo" placeholder="账号" :error-message="accountNoErr" required
                   @keyup.enter.native="loginSubmit">
          <van-icon slot="left-icon" class-prefix="iconfont icon-wode-weixuanzhong"/>
        </van-field>
      </div>

      <div class="formItem">
        <van-field v-model="password" type="password" placeholder="密码" :error-message="passwordErr" required>
          <van-icon slot="left-icon" class-prefix="iconfont icon-mima"/>
        </van-field>
      </div>

      <div class="formItem">
        <van-field v-model="code" placeholder="验证码" :error-message="codeErr" required @keyup.enter.native="loginSubmit">
          <van-icon slot="left-icon" class-prefix="iconfont icon-yanzhengma"/>
          <div slot="button">
            <!--            <img :src="codeImg" alt="重新加载" :style="{ width: '9.75rem' }" @click="getCode"/>-->
            <van-image class="item-code" fit="contain" :src="codeImg" @click="getCode">
              <template v-slot:error>
                <van-loading v-if="loadCode" type="spinner" size="20"/>
                <span v-else>加载失败</span>
              </template>
            </van-image>
          </div>
        </van-field>
      </div>
    </div>
    <div class="footer">
      <van-button type="primary" :loading="loading" @click="loginSubmit" :disabled="disabled" loading-text="登录中...">
        登录
      </van-button>
      <div class="footer_operate">
        <!--        <span @click="show=true">免密进入</span>-->
        <span></span>
        <span @click="showTypeChange(2)">用户注册</span>
      </div>
    </div>

    <van-popup v-model="show" position="bottom">
      <van-cell value="扫一扫" @click.stop.prevent="toScan">
        <van-icon slot="right-icon" name="scan"></van-icon>
      </van-cell>
      <van-cell value="班级码" @click.stop.prevent="toCode">
        <van-icon slot="right-icon" class-prefix="iconfont icon-yanzhengma1"></van-icon>
      </van-cell>
    </van-popup>

    <!-- 输入班级码免密进入 -->
    <van-popup v-model="codeVisible">
      <r-digit-input ref="digit" title="请输入班级码" sub-title="请在下方输入班级码连接教室" :code-length="6" btn-name="连接教室"
                     code-name="班级码" @submit="submitCode">
      </r-digit-input>
    </van-popup>

    <!-- 来宾账户选择-->
    <van-popup v-model="guestShow" position="bottom" closeable>
      <div class="sub_nav">
        <span>切换来宾账号</span>
      </div>
      <van-cell-group>
        <van-cell class="cell_main" v-for="(item, index) in guestList" is-link :key="index" @click="guestSelect(item)">
          <template slot="icon">
            <img :src="defaultHead" alt=""/>
          </template>
          <template slot="title">
            <span class="line_title">{{item.name}}</span>
          </template>
        </van-cell>
      </van-cell-group>
    </van-popup>
  </div>
</template>

<script>
import RDigitInput from "@/components/r-digit-input";
import md5 from "js-md5";
import defaultImg from "../../../assets/boy_img.png";
import { FuncType, sendToAPP } from "@/utils/interFlow";
import { encrypt, decryption } from "@/utils/index";
import { LoginPlatform } from "@/utils/enum";

export default {
  name: "r-login",
  components: { RDigitInput },
  created () {
  },
  data () {
    return {
      accountNo: this.$store.getters.userName || "",
      accountNoErr: "",
      password: decryption(this.$store.getters.userPassword || "") || "",
      passwordErr: "",
      code: "",
      codeErr: "",
      codeID: "",
      disabled: false,
      codeImg: "",
      loading: false,
      loadCode: false,
      show: false,
      codeVisible: false,

      guestList: [],
      defaultHead: defaultImg,
      guestShow: false
    };
  },
  mounted () {
    if (!this.$store.getters.serverIP) {
      this.showTypeChange(0);
    } else {
      this.getCode();
    }

    window.acceptQRcode = code => {
      console.log("接收扫码：", code);
      this.submitScan(code);
      return true;
    };
  },
  methods: {
    getCode () {
      this.codeImg = "";
      this.loadCode = true;
      this.$mainHttp.account.getLoginCode().then(res => {
        if (res.Result && res.Datas) {
          this.codeID = res.Datas.slice(0, 32);
          this.codeImg = `data:image/png;base64,${res.Datas.slice(32)}`;
        }
        this.loadCode = false;
      }).catch(() => {
        this.codeImg = "";
        this.loadCode = false;
      });
    },
    showTypeChange (val) {
      this.$emit("change", val);
    },

    loginSubmit () {
      this.accountNoErr = "";
      this.passwordErr = "";
      if (!this.$store.getters.serverIP) {
        this.$toast("请先设置服务地址");
        this.showTypeChange(0);
        return;
      }

      if (this.accountNo === "") {
        this.accountNoErr = "请输入登录账号";
        return;
      }
      if (this.password === "") {
        this.passwordErr = "请输入登录密码";
        return;
      }

      if (this.code === "") {
        this.codeErr = "请输入验证码,不区分大小写";
        return;
      }
      this.codeValidate();
    },
    codeValidate () {
      this.$mainHttp.account.postCodeValidate({
        code: this.code
      }, this.codeID).then(res => {
        if (res.Result) {
          this.LoginRequest();
        } else if (res.Info) {
          this.codeErr = res.Info;
        } else {
          this.codeErr = "验证码错误";
        }
      }).catch(() => {
        this.codeErr = "验证码错误";
      });
    },
    LoginRequest () {
      if (this.loading) return;
      this.loading = true;
      this.disabled = true;
      this.$store
        .dispatch("LoginByUser", {
          userInfo: {
            name: this.accountNo,
            pwd: md5(this.password), // .toUpperCase()
            code: this.code,
            from: window.webkit ? LoginPlatform.TeacherIos : LoginPlatform.TeacherAndroid // "11"
          },
          other: this.codeID
        }).then(res => {
        if (res.Result) {
          // 账户为来宾账户，并且已被登录使用
          // eslint-disable-next-line no-prototype-builtins
          if (res.hasOwnProperty("IsGuestLock") && res["IsGuestLock"]) {
            this.$toast("该来宾账号已被占用，请更换");
            this.getGuestList();
            return;
          }
          if (res.Teacher) {
            this.$store.commit("SET_USER_PASSWORD", encrypt(this.password));
            this.$router.push("/index");
            sendToAPP(FuncType.hadLogin, "");
          } else {
            this.$toast("登录出错，请重试");
            this.getCode();
          }
        } else if (res.Info) {
          this.$toast("账号或密码错误");
          this.getCode();
        } else {
          this.$toast("请输入正确账号和密码");
          this.getCode();
        }
        this.disabled = false;
        this.loading = false;
      }).catch(() => {
        this.disabled = false;
        this.loading = false;
      });
    },

    toScan () {
      sendToAPP(FuncType.openQRcode, "");
    },
    submitScan (code) {
      console.log("code:", code);
      this.$store.commit("SET_CODEKEY", "11112222");
      this.$router.push("/course");
    },
    toCode () {
      this.show = false;
      this.codeVisible = true;
      setTimeout(() => {
        this.$refs["digit"].init();
      }, 0);
    },
    submitCode (arr) {
      console.log("arr:", arr);
      this.$store.commit("SET_CODEKEY", "11112222");
      this.$router.push("/course");
    },
    // 获取来宾账户
    getGuestList () {
      this.guestList = [];
      this.code = "";
      this.getCode();
      this.disabled = false;
      this.loading = false;
      this.$mainHttp.account.getGuestList().then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.guestList.push({
                id: item["UserID"],
                name: item["UserName"]
              });
            });
          }
        }
        this.guestShow = true;
      }).catch(() => {
        this.guestShow = true;
      });
    },
    guestSelect (item) {
      this.accountNo = item.name;
      this.guestShow = false;
    }
  }
};
</script>
