/**
 * @param {string} str
 * @returns {Boolean}
 */
export function isString (str) {
  return typeof str === "string" || str instanceof String;
}

/**
 * @param {Array} arg
 * @returns {Boolean}
 */
export function isArray (arg) {
  if (typeof Array.isArray === "undefined") {
    return Object.prototype.toString.call(arg) === "[object Array]";
  }
  return Array.isArray(arg);
}

/**
 * 校验密码强度
 * @param {*} value 密码
 */
export function checkPassWord (value) {
  // 0： 表示第一个级别 1：表示第二个级别 2：表示第三个级别
  // 3： 表示第四个级别 4：表示第五个级别
  let modes = 0;
  // 最初级别
  if (value.length < 6) {
    return modes;
  }
  // 如果用户输入的密码 包含了数字
  if (/\d/.test(value)) {
    modes++;
  }
  // 如果用户输入的密码 包含了小写的a到z
  if (/[a-z]/.test(value)) {
    modes++;
  }
  // 如果用户输入的密码 包含了大写的A到Z
  if (/[A-Z]/.test(value)) {
    modes++;
  }
  // 如果是非数字 字母 下划线
  if (/\W/.test(value)) {
    modes++;
  }
  switch (modes) {
    case 1:
      return 1;
    case 2:
      return 2;
    case 3:
      return 3;
    case 4:
      return 4;
    default:
      return 0;
  }
}
