<template>
  <div class="login-wrap">
    <div class="login_setting">
      <van-icon name="setting-o" @click="showTypeChange(0)"/>
    </div>
    <div class="logo"></div>
    <div class="loginForm">
      <div class="formItem">
        <van-field v-model="accountNo" placeholder="教工号" :error-message="accountNoErr" required>
          <van-icon slot="left-icon" class-prefix="iconfont icon-gerenziliao"/>
        </van-field>
      </div>

      <div class="formItem">
        <van-field v-model="username" placeholder="姓名" :error-message="usernameErr" required>
          <van-icon slot="left-icon" class-prefix="iconfont icon-wode-weixuanzhong"/>
        </van-field>
      </div>

      <div class="formItem">
        <van-field v-model="password" type="password" placeholder="密码" :error-message="passwordErr" required>
          <van-icon slot="left-icon" class-prefix="iconfont icon-mima"/>
          <span v-if="strongIndex > -1" slot="right-icon" :style="{ color: passwordStrongColor[strongIndex] }">
            {{ passwordStrongArr[strongIndex] }}
          </span>
        </van-field>
      </div>
      <div class="formItem">
        <van-field v-model="rePassword" type="password" placeholder="确认密码" :error-message="rePasswordErr" required
                   @keyup.enter.native="registerSubmit">
          <van-icon slot="left-icon" class-prefix="iconfont icon-mima"/>
        </van-field>
      </div>
    </div>
    <div class="footer">
      <van-button type="primary" :loading="loading" @click="registerSubmit" :disabled="disabled" loading-text="注册中...">
        {{ btnName }}
      </van-button>
      <div class="footer_operate">
        <span @click="showTypeChange(1)">返回登录</span>
      </div>
    </div>
  </div>
</template>

<script>
import { checkPassWord } from "@/utils/validate";

export default {
  name: "r-register",
  components: {},
  created () {
  },
  data () {
    return {
      accountNo: "",
      accountNoErr: "",
      username: "",
      usernameErr: "",
      password: "",
      passwordErr: "",
      rePassword: "",
      rePasswordErr: "",
      teacherID: "",
      btnName: "注册",
      disabled: false,
      loading: false,

      passwordStrongArr: ["极弱", "弱", "中", "强", "很强"],
      passwordStrongColor: [
        "#ee0a24",
        "#FF9900",
        "#1989fa",
        "#33CC00",
        "#07c160"
      ]
    };
  },
  computed: {
    strongIndex () {
      return this.password.length === 0 ? -1 : checkPassWord(this.password);
    }
  },
  methods: {
    showTypeChange (val) {
      this.$emit("change", val);
    },
    registerSubmit () {
      this.accountNoErr = "";
      this.usernameErr = "";
      this.passwordErr = "";
      this.rePasswordErr = "";
      if (this.accountNo === "") {
        this.accountNoErr = "请输入教工号";
        return;
      }
      if (this.username === "") {
        this.usernameErr = "请输入姓名";
        return;
      }
      if (this.password === "") {
        this.passwordErr = "请输入登录密码";
        return;
      }
      if (this.strongIndex < 2) {
        this.passwordErr = "密码强度过低，可使用字母、特殊字符增加强度";
        return;
      }
      if (this.rePassword === "") {
        this.rePasswordErr = "请输入确认密码";
        return;
      }
      if (this.rePassword !== this.password) {
        this.rePasswordErr = "两次输入的密码不一致";
        return;
      }
      if (!this.$store.getters.serverIP) {
        this.$toast("请先设置服务地址");
        this.showTypeChange(0);
        return;
      }
      this.loading = true;
      this.disabled = true;
      this.verifyUserName();
    },
    verifyUserName () {
      this.$mainHttp.teacher.getTeacherValidate({
        name: this.username, tno: this.accountNo
      }).then(res => {
        if (res.Result) {
          this.verifyUser();
        } else if (res.Info) {
          this.$toast(res.Info);
          this.disabled = false;
          this.loading = false;
        } else {
          this.accountNoErr = "教工号或姓名不正确";
          this.$toast("教工号或姓名不正确");
          this.disabled = false;
          this.loading = false;
        }
      }).catch(() => {
        this.disabled = false;
        this.loading = false;
      });
    },
    verifyUser () {
      this.$mainHttp.teacher.getVerifyData({
        tno: this.accountNo
      }).then(res => {
        if (res.Result) {
          this.teacherID = res.Data["TeacherID"];
          this.postRegister();
        } else if (res.Info) {
          this.$toast(res.Info);
          this.disabled = false;
          this.loading = false;
        } else {
          this.accountNoErr = "教工号不正确";
          this.$toast("请确认教工号");
          this.disabled = false;
          this.loading = false;
        }
      }).catch(() => {
        this.disabled = false;
        this.loading = false;
      });
    },
    postRegister () {
      const reqData = {
        name: this.accountNo,
        id: this.teacherID,
        type: 1,
        pwd: this.password
      };
      this.$mainHttp.account.postRegister(reqData).then(res => {
        if (res.Result) {
          this.$toast("注册成功");
          this.btnName = "重新注册";
          this.$store.commit("SET_USER", this.accountNo);
          this.$store.commit("SET_USER_PASSWORD", "");

          this.disabled = false;
          this.loading = false;
          setTimeout(() => {
            this.showTypeChange(1);
          }, 500);
        } else if (res.Info) {
          this.$toast(res.Info);
          this.disabled = false;
          this.loading = false;
        } else {
          this.$toast("注册失败");
          this.disabled = false;
          this.loading = false;
        }
      }).catch(() => {
        this.disabled = false;
        this.loading = false;
      });
    }
  }
};
</script>
